@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root{
    font-size: 62.5%
}

body{
    font-family: 'Roboto';
}

::-webkit-scrollbar {
    display: none;
}
  



.App{
    display: flex;
}

.main{
    flex: 1 0;
    margin-left: 10rem;
    width: 100px;
    height: 100vh;
    background: #A9BCD0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.name{
    font-size: 6.4rem;
    color: #1B1B1E;
}

.name-section{
    display: flex;
    flex-direction: column;
}

.socials-logo{
    margin: .1rem 1.8rem;
    width: 4.9rem;
    height: 4.9rem;
    fill: #1B1B1E;
}

.icon-holder{
    display: flex;
    justify-content: center;
}

.watermark {
    position: fixed;
    bottom: .5rem;
    right: .5rem;
    color: #1B1B1E;
    font-weight: 400;
    font-size: 1.2rem
}

.scroll-down{
    position: absolute;
    bottom: 1rem;
}

.arrow-down-icon{
    fill:  #1B1B1E;
    width: 2.4rem;
}

.nav-ul{
    position: absolute;
    top: 20%;
    left: 25%;
    transform: translate(-25%);
    list-style: none;
}

.nav-link{
    margin-bottom: 3.1rem;
}

.nav-img {
    width: 4.9rem;
    height: 4.9rem;
    margin-left: 25%;
    fill: #D8DBE2;
}

.nav-logo{
    position: absolute;
    width: 7.9rem;
    height: 7.9rem;
    border-radius: 50%;
    border: .2rem solid #D8DBE2;
    left: 50%;
    top: 1.6rem;
    transform: translate(-50%, -10%)
}

.nav-logo + h1{
    display: none;
}

.nav-img + h1{
    display: none;
}

.nav-bar{
    position: fixed;
    align-items: center;
    background: #1B1B1E;
    width: 10rem;
    height: 100vh;
    top: 0;
    left: 0;
}
